:root {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: initial;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  cursor: pointer;
  background-color: inherit;
  color: inherit;
}
